import './Section1.css'
import React from 'react'
import picture1 from './pictures/image-mockups.png'
const Section1 = () => {
    return (
        <div class="section1">
            <div class="wrapper-section1">
                <img src={picture1} id="section1img"></img>
                <div class="text-section">
                    <h1>Next generation digital banking</h1>
                    <p>Take your financial life online. Your Easybank account will be a one-stop-shop for spending, saving, budgeting
                        , investing, and much more.
                    </p>
                    <div class="button-section1">Request invite</div>
                </div>
            </div>
        </div>
    )
}
export default Section1
