import React from 'react'
import './Navbar.css'
import logo from "./pictures/logo.svg"
import  { useState } from 'react';
import {Squash as Hamburger } from 'hamburger-react'
const Navbar = () => {
    const [state, setstate] = useState("open nav")
    function reverse(){
        if (state=="close nav"){
            setstate("open nav")
        }else{
            setstate("close nav")
        }
    } 
    return (
        <div class="navbar">
            <div class="wrapper-navbar">
                <div class="logo-navbar"><img src={logo}></img></div>
                <button onClick={() => reverse()}><Hamburger/></button>
            </div>
                <div class={state}>
                    <ul>
                        <li>Home</li>
                        <li>About</li>
                        <li>Contact</li>
                        <li>Blog</li>
                        <li>Careers</li>
                    </ul>
                </div>
        </div>
    )
}

export default Navbar
